<template>
  <Card class="card">
    <Breadcrumb>
      <BreadcrumbItem>市场管理</BreadcrumbItem>
      <BreadcrumbItem>油价</BreadcrumbItem>
    </Breadcrumb>
    <div class="box-container">

       <Collapse v-model="value100" accordion style="margin:20px 0;width:80%" >
        <Panel name="1">
           <span>平台油价信息</span>
           <Row slot="content" class="row">
              <Col span="2">油型种类</Col>
              <Col span="10"><Input style="width:220px" placeholder='请输入油型种类' v-model="oilType" /></Col>
              <Col span="2">油型油价</Col>
              <Col span="10">
                <Input style="width:220px" placeholder='请输入油价' v-model="oilPrice" />
                 <Button type="primary" @click="save('save')" size='small'>保存</Button>
              </Col>
          </Row>
           <Row slot="content">
             <table class="table table-row" style="width:70%">
                 <tr>
                     <td>油型种类</td>
                     <td>油价</td>
                     <td>操作</td>
                 </tr>
                 <tr v-for="(list,i) in platform" :key="i">
                    <td  v-if="!list.isEditor">{{list.fuelType}}</td>
                    <td v-else> <Input style="width:100px" placeholder="请输入油型种类" v-model="list.fuelType" /></td>
                    <td  v-if="!list.isEditor">{{list.price}}</td>
                    <td v-else> <Input style="width:100px " placeholder="请输入油价" v-model="list.price" /></td>
                    <td v-if="!list.isEditor"> <Button type="primary" size='small' @click="update(i,list.code)"> 修改</Button> </td>
                    <td v-else> <Button type="primary" size='small' @click="save(i)"> 保存</Button> </td>
                 </tr>
             </table>
          </Row>

        </Panel>
      </Collapse>
      <Row class="row">
        <Button type="primary" @click="addShopper">添加供应商</Button>
        <Modal
          v-model="showModal"
          :title="title"
          @on-ok="ok"
          @on-cancel="cancel"
        >
        <Row class="row">
            <Col span="3">供应商</Col>
            <Col span="10"> <Input placeholder="请输入供应商" style="width:240px" v-model="companyName" /></Col>
        </Row>

        </Modal>
      </Row>
       <Collapse v-model="values" accordion style="margin-top:20px;width:80%" >
        <Panel :name="list.value" v-for="(list,index) in providerInfo" :key='index'>
           <span>{{list.companyName}}</span>
           <Button type="primary" class="update" @click.stop.prevent="updatefuns(list.companyName,list.fuelCompanyCode)" size='small'>修改</Button>
           <Button type="warning" class="delete" @click.stop.prevent="deleteFun(list.fuelCompanyCode)" size='small'>删除</Button>
           <Row slot="content" class="row" v-for="(item,indx) in list.oilArr" :key='indx'>
              <Col span="2">   {{indx==0 ? '油型种类':'&nbsp'}}  </Col>
              <Col span="10"><Input style="width:220px" placeholder='请输入油型种类' v-model="item.fuelType" /></Col>
              <Col span="2"> {{indx==0 ? '油型油价':'&nbsp'}}</Col>
              <Col span="10">
                <Input style="width:220px" placeholder='请输入油价' v-model="item.price" />
                <Icon type="md-add-circle" style="font-size:20px" @click="addOil(index)"  v-if="indx == 0" />
                <Icon type="ios-remove-circle" style="font-size:20px" @click="minusOil(index)"  v-if="indx" />
                 <Button type="primary" v-if="!indx" @click="newSave(index,list.fuelCompanyCode)" size='small'>保存</Button>
              </Col>
          </Row>
           <Row slot="content">
             <table class="table table-row" style="width:90%">
                 <tr>
                     <td>#</td>
                     <td>油型种类</td>
                     <td>油价</td>
                     <td>日期</td>
                     <td>操作</td>
                 </tr>
                 <tr v-for="(key,i) in list.fuelCompanyPrices " :key="i">
                     <td>{{i+1}}</td>
                     <td v-if="!key.isEditor">{{key.fuelType}}</td>
                     <td v-else> <Input style="width:100px" placeholder="请输入油型种类" v-model="key.fuelType"  /></td>
                     <td v-if="!key.isEditor">{{key.price}}</td>
                     <td v-else> <Input style="width:100px " placeholder="请输入油价" v-model="key.price" /></td>
                     <td >{{key.createTime}}</td>
                     <td v-if="!key.isEditor">
                        <Button type="primary" size='small' @click="updateFn(index,i)">修改</Button>
                         <Button type="warning" size='small' @click="deleteFns(key.code)" >删除</Button>
                      </td>
                     <td v-else>   <Button type="primary" size='small'  @click="saveFn(index,i,key.code)">保存</Button> </td>
                 </tr>
             </table>
          </Row>

        </Panel>
      </Collapse>
    </div>
  </Card>
</template>
<script>
export default {
  data() {
    return {
      value100: "1",
      showModal: false,
      platform:[],
      oilType:'',
      oilPrice:'',
      providerInfo:[],
      companyName:'',
      title:"添加供应商",
      status:1,
      id:'',
      values:'1'
    };
  },
  methods: {
    update(index){
          this.platform[index]['isEditor'] = true
    },
    addShopper() {
      this.showModal = true;
      this.status = 1
      this.title='添加供应商'
      localStorage.removeItem('content')
    },
    addOil(index){
      const obj = {
        price:'',
        fuelType:'',
      }
      this.providerInfo[index].oilArr.push(obj)
    },
    minusOil(index){
        this.providerInfo[index].oilArr.splice(index,1)
    },
    ok(name,id,) {

     if(this.status == 1){
         this.addFuelComapany()
     }
     else{

       this.addFuelComapany(this.companyName,this.id,'update')
     }
    },
    cancel() {
      // this.$Message.info("取消");
      this.companyName = ''
    },
    // 平台油价详情
    async getPlatformFuelPriceInfo(){
       let res = await this.$http.getPlatformFuelPriceInfo()
        this.platform = []
        var result = res.data.result
       if(!res.data.status && res.data.result instanceof Array){
           for(let i in result){
                result[i].isEditor = false
            }
            this.platform = result
            // console.log(result,'result')

       }
    },
   async save(index){
     let data ;
     if(index=='save'){


       let addData = {
         fuelType:this.oilType,
         price:this.oilPrice,
       }
       data = addData
     }
     else{

       let { fuelType,price}  = this.platform[index]
       let editorData = {
         fuelType,
         price,
       }
        data = editorData
     }
      // console.log(data)
      let res = await this.$http.addPlatformPrice(data)
      if(0==res.data.status){
        let desc = index == 'save'?"保存成功":"修改成功"
        this.$Notice.success({
          desc
        })
        this.getPlatformFuelPriceInfo()
        this.oilPrice = ''
        this.oilType = ''
        if(index == 'save') return
        this.platform[index]['isEditor'] = false
      }
      else{
          this.$Notice.error({
          desc:res.data.msg
        })
      }

    },

    async getFuelComapanyPriceInfo(){
        let res = await this.$http.getFuelComapanyPriceInfo()
        this.providerInfo = []
        var result = res.data.result
        if(!res.data.status){
           for(let i in result){
              for(let j in result[i]['fuelCompanyPrices']){
                result[i]['fuelCompanyPrices'][j].isEditor = false
                result[i]['fuelCompanyPrices'][j].date = result[i]['fuelCompanyPrices'][j].createTime ? result[i]['fuelCompanyPrices'][j].createTime.split(' ')[0]:""
                result[i]['fuelCompanyPrices'][j].time = result[i]['fuelCompanyPrices'][j].createTime ? result[i]['fuelCompanyPrices'][j].createTime.split(' ')[1]:""
              }
            }
            result.map((item,index)=>{
                  this.providerInfo.push(
                      Object.assign({},item,{
                        value:index+1+'',
                        oilArr:[
                          {
                            fuelType:'',
                            price:'',
                          }
                        ],
                        isCollapse:false
                      })
                  )
            })
        }
        //  console.log(result,'result')
    },
       // 处理日期
    handleDate(event,outIndex,index,params){
       if(params == 'date'){
          this.providerInfo[outIndex].fuelCompanyPrices[index].date= event
       }
       else{
         this.providerInfo[outIndex].fuelCompanyPrices[index].time= event
       }
    },
   updateFn(outIndex,i){
      this.providerInfo[outIndex].fuelCompanyPrices[i].isEditor = true
   },
   async saveFn(outIndex,index,id){
     let {fuelType,price} = this.providerInfo[outIndex].fuelCompanyPrices[index]
     let data  = {
       fuelType,
       price,
       fuelCompanyPriceCode:id
     }
     let res = await this.$http.updateFuelComapanyPrice(data)
     if(!res.data.status){
       this.$Notice.success({
         desc:'修改成功'
       })
           this.providerInfo[outIndex].fuelCompanyPrices[index].isEditor = false
             this.getFuelComapanyPriceInfo()
     }
     else{
         this.$Notice.success({
         desc:res.data.msg
       })
     }
   },
   async newSave(index,id){
     let {oilArr} = this.providerInfo[index]
      let data = {
        fuelCompanyPrice:oilArr,
        fuelCompanyCode:id,
      }

      let res = await this.$http.addFuelComapanyPrice(data)
       if(!res.data.status){
         this.$Notice.success({
           desc:'保存成功'
         })
          this.getFuelComapanyPriceInfo()
      }else{
         this.$Notice.error({
           desc:res.data.msg
         })
      }
   },
  //  添加供应商
  async addFuelComapany(companyName,id,params){
    if(this.companyName==localStorage.content ){
      this.$Notice.error({
           desc:'修改供应商内容与原内容相同，请重新输入.'
         })
         this.companyName = ''
         return false
    }
    let data  = {
      companyName:this.companyName,
      fuelCompanyCode:id||'',
    }
      this.companyName  = ""
      let res = await this.$http.addFuelComapany(data)
      console.log(res)
      if(!res.data.status){
        let msg = params ? '修改成功':"添加成功"
         this.$Notice.success({
           desc:msg
         })
          this.companyName  = ""
          this.getFuelComapanyPriceInfo()
      }else{
         this.$Notice.error({
           desc:res.data.msg
         })
      }
   },
   updatefuns(name,id){
     this.showModal = true
     this.companyName = name
     this.title = '修改供应商'
     this.status = 2
     this.id = id
    //  this.ok(name,id,'update')
    //  this.addFuelComapany (name,id,'update')
     localStorage.setItem('content',name)
   },
   async deleteFun(id){
     let data  = {
      fuelCompanyCode: id
     }
     let res = await this.$http.deleteFuelComapany(data)
     if(!res.data.status){
       this.$Notice.success({
         desc:'删除成功'
       })
        this.getFuelComapanyPriceInfo()
     }else{
        this.$Notice.error({
         desc:res.data.msg
       })
     }
   },
   async deleteFns(id){
      let data = {
        fuelComapanyPriceCode:id
      }
      let res =  await this.$http.deleteFuelComapanyPrice(data)
      // console.log(res)
      if(!res.data.status){
        this.$Notice.success({
          desc:"删除成功"
        })
        this.getFuelComapanyPriceInfo()
      }
      else{
         this.$Notice.error({
          desc:res.data.msg
        })
      }
    }
  },
  created(){
    this.getPlatformFuelPriceInfo()
    this.getFuelComapanyPriceInfo()
  }
};
</script>
<style lang="scss" scoped>
.table-row{
  margin: 20px 0px;
}
.row{
  line-height: 45px;
}
.update{
  position: absolute;
  right:120px;
  top: 5px;
  z-index: 900000;

}
.delete{
  top: 5px;
  position: absolute;
  right: 20px;
  z-index: 888000;
}
.table, tr ,td{
  border: 1px solid #e4e4ee;
  border-collapse: collapse;
  padding: 8px 30px;
  text-align: center;
}
</style>



